.adventures
{
    padding-top: 120px;
    padding-bottom: 120px;

    background-color: $color-alice-blue;

    @include vp-1365
    {
        padding-top: 92px;
        padding-bottom: 77px;
    }
    @include vp-767
    {
        padding-top: 60px;
        padding-bottom: 65px;
    }
}

.adventures__container
{
    display: grid;

    grid-template-columns: repeat(2, 1fr);
    gap: 15px 40px;
    @include vp-1365
    {
        column-gap: 30px;
    }
    @include vp-767
    {
        display: flex;
        flex-direction: column;
    }
}

.adventures__text
{
    margin-top: 56px;

    @include vp-1365
    {
        margin-top: -9px;
    }
}

.adventures h2
{
    font-size: 28px;
    font-weight: 600;
    line-height: 42px;

    margin-top: 0;

    @include vp-1365
    {
        font-size: 26px;
        line-height: 36px;

        max-width: 93%;
        margin-bottom: 16px;
    }
    @include vp-767
    {
        font-size: 24px;
        line-height: 34px;
    }
}

.adventures p
{
    font-size: 16px;
    line-height: 24px;

    margin-top: 0;
}

.adventures__image
{
    width: 100%;
}

.adventures img
{
    width: 100%;
    height: 301px;
    margin-right: auto;
    margin-left: auto;

    background-color: $color-dark-cerulean;

    object-fit: cover;
    object-position: 50% 0;
    @include vp-1365
    {
        height: 264px;
    }
    @include vp-767
    {
        height: 186px;

        object-position: 50% 0;
    }
}
