.contacts
{
    padding-top: 120px;
    padding-bottom: 120px;

    background-color: $color-alice-blue;

    @include vp-1365
    {
        padding-top: 100px;
        padding-bottom: 100px;
    }
    @include vp-767
    {
        padding-top: 40px;
        padding-bottom: 63px;
    }
}

.contacts__container
{
    display: grid;

    grid-template-columns: repeat(2, 1fr);
    gap: 100px 42px;
    @include vp-1365
    {
        column-gap: 30px;
    }
    @include vp-767
    {
        display: flex;
        flex-direction: column-reverse;

        gap: 0;
    }
}

.contacts__container p
{
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;

    margin-bottom: 28px;

    @include vp-1365
    {
        margin-bottom: 22px;
    }
}

.contacts__container address
{
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    line-height: 24px;

    margin-bottom: 20px;
    padding-left: 5px;
}

.contacts__container a
{
    font-size: 16px;
    line-height: 24px;

    margin-bottom: 20px;
    padding-left: 5px;

    &:hover
    {
        opacity: .7;
    }

    &:focus
    {
        opacity: .5;
        outline: none;
    }

    &:active
    {
        opacity: .3;
    }
}

.contacts__info
{
    max-width: 500px;
    margin-top: 56px;
    margin-left: auto;

    @include vp-1365
    {
        margin-top: 49px;
    }
    @include vp-767
    {
        margin-top: 0;
        margin-right: auto;
        margin-bottom: 38px;
    }
}
