.wrapper
{
    position: relative;

    display: flex;
    flex-direction: column;

    min-height: 100vh;
    /* stylelint-disable-next-line */
    min-height: calc(100 * var(--vh, 1vh));

    .header,
    .footer
    {
        flex-shrink: 0;
    }

    main
    {
        flex-grow: 1;
    }
}

.visually-hidden
{
    position: absolute;

    overflow: hidden;
    clip: rect(0 0 0 0);

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;

    white-space: nowrap;

    border: 0;

    clip-path: inset(100%);
}
