.social
{
    display: flex;

    margin-left: auto;

    @include vp-767
    {
        margin-left: 0;
    }
}


.social__container
{
    display: flex;
    flex-wrap: wrap;

    padding-top: 9px;
    padding-bottom: 9px;

    gap: 28px;
    @include vp-1365
    {
        padding-top: 20px;
        padding-bottom: 20px;

        gap: 23px;
    }
    @include vp-767
    {
        padding-top: 15px;
    }
}

.social__container a
{
    &:hover
    {
        opacity: .7;
    }

    &:focus
    {
        opacity: .5;
        outline: none;
    }

    &:active
    {
        opacity: .3;
    }
}

.social__icon--facebook svg
{
    @include vp-1365
    {
        width: 9px;
        height: 16px;
    }
}

.social__icon--instagram svg
{
    @include vp-1365
    {
        width: 16px;
        height: 16px;
    }
}

.social__icon--twitter svg
{
    @include vp-1365
    {
        width: 16px;
        height: 13px;
    }
}
